<template>
  <va-card
    :color="$themes.background"
    :style="computedWidgetStyle"
    :title="$t('dashboard.totalLog.title')"
  >
    <va-button
      slot="actions"
      color="info"
      class="mr-0"
      small
      flat
      @click="refresh"
    >
      <va-icon name="material-icons">autorenew</va-icon>
    </va-button>
    <va-inner-loading :loading="log.length && loading">
      <div v-if="log.length" class="tab" :style="computedStyle">
        <div class="row">
          <div class="flex xs12 xl12">
            <div class="tab__wrap">
              <div class="tab__wrap__title">
                <div>Action</div>
                <div>User</div>
                <div>Date</div>
              </div>
              <div class="tab__wrap__table">
                <div
                  v-for="action in log[activePage - 1]"
                  :key="action._id"
                  class="tab__wrap__table__item align--center"
                >
                  <div class="tab__wrap__table__item__title">
                    <div>Action</div>
                    <div>User</div>
                    <div>Date</div>
                  </div>
                  <div class="tab__wrap__table__item__props">
                    <div class="tab__wrap__table__item__props-action">
                      <va-icon
                        :name="actionIcon(action.actionName)"
                        class="ml-3 mr-3 "
                      ></va-icon>
                      {{ $t(action.messageLocalPath) }}
                      {{ action.relatedData }}
                    </div>
                    <div class="tab__wrap__table__item__props-name">
                      {{ action.user.name }}
                    </div>
                    <div class="tab__wrap__table__item__props-date">
                      {{ action.formatedDate }}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="tab__pagination_wrap">
          <va-pagination
            v-model="activePage"
            :pages="log.length"
            :visiblePages="5"
            small
          ></va-pagination>
        </div>
      </div>
      <VaCardNoData
        v-else
        :loading="loading"
        LT="Log is loading..."
        ET="Empty"
      />
    </va-inner-loading>
  </va-card>
</template>

<script>
import VaCardNoData from './components/VaCardNoData'
import _ from 'lodash'
import { ActionsList } from '../../services/Users'

export default {
  name: 'dashboard-total-log',
  components: {
    VaCardNoData,
  },
  data: () => ({
    loading: false,
    activePage: 1,
    actions: [],
  }),
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    computedWidgetStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
    log() {
      return _.chunk(this.actions, 8)
    },
  },
  methods: {
    refresh() {
      this.loading = true
// 
      // this.actions = []
      // for (let i = 0; i < 30; i++) {
      // this.actions.push({
      // _id: i,
      // user: {
      // name: ' ',
      // },
      // relatedData: '',
      // messageLocalPath:
      // 'User has changed his',
      // actionName: 'visited',
      // formatedDate: '17.10.20 1:32:06',
      // })
      // }
// 
      ActionsList().then(({ data: actions }) => {
        this.loading = false
        this.actions = actions
      })
    },
    actionIcon(name) {
      return {
        registered: 'fa fa-user-plus',
        authenticated: 'fa fa-sign-in',
        visited: 'fa fa-map-marker',
        name_changed: 'fa fa-edit',
        transfer: 'fa fa-money',
        withdrawal: 'fa fa-money',
        deposit: 'fa fa-money',
      }[name]
    },
  },
  mounted() {
    this.refresh()
  },
}
</script>

<style lang="scss" scoped>
@import '../../sass/smart-grid.scss';

.tab {
  min-height: 232px;

  &__wrap {
    &__title {
      font-weight: 600;
      width: 100%;
      height: 100%;

      @include to(80rem) {
        display: none;
      }

      @include from(80rem) {
        height: 40px;
        display: grid;
        justify-items: center;
        align-items: center;
        grid-template-columns: 1fr 200px 150px;
      }
    }

    &__table {
      &__item {
        width: calc(100% + 40px);
        margin: 0 -20px;

        @include to(80rem) {
          display: grid;
          grid-template-columns: 70px 1fr;
        }

        @include from(80rem) {
          display: block;
        }

        &__title {
          height: 100%;

          div {
            display: grid;
            align-content: center;
            text-indent: 10px;
            width: 100%;
            height: 100%;

            border: 1px #ffffff11 solid;
          }

          @include to(80rem) {
            display: grid;
            grid-template-rows: 2fr 1fr 1fr;
            justify-items: center;
            align-items: center;
          }

          @include from(80rem) {
            display: none;
          }
        }

        &__props {
          min-height: 55px;

          @include to(80rem) {
            display: grid;
            grid-template-rows: 2fr 1fr 1fr;
          }

          @include from(80rem) {
            display: grid;
            grid-template-columns: 1fr 200px 150px;
          }

          div {
            width: 100%;
            height: 100%;
            display: flex;
            padding: 10px;
            border: 1px #ffffff11 solid;
          }

          &-action {
            align-items: center;
          }

          &-date,
          &-name {
            text-align: center;

             @include to(80rem) {
              align-items: center;
              justify-content: flex-start;
            }

            @include from(80rem) {
              align-items: center;
              justify-content: center;
            }
          }
        }
        &:nth-child(odd) {
          background-color: #ffffff07;
        }
      }
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: center;
    height: 2rem;
  }
}
</style>
